.Page {
    background-color: white;
    min-height : 46rem;
    max-height : 46rem;
    height : 46rem;

    overflow-y: auto;

    @media screen and (max-height : 800px) {
        min-height : 36rem;
    }

    @media screen and (max-height : 1000px) {
        min-height : 44rem;
    }

    @media screen and (max-height : 1200px) {
        min-height : 46rem;
    }
    
    @media screen and (min-height : 1599px) {
        min-height : 85rem;
    }

    @media screen and (min-height : 1800px) {
        min-height : 115rem;
    }
}